"use client";

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';

interface AdminLayoutProps {
  children: React.ReactNode;
  title?: string;
}

export function AdminLayout({ children, title = "Admin Dashboard" }: AdminLayoutProps) {
  const router = useRouter();
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const [companySettings, setCompanySettings] = useState({
    name: 'FrameShop',
    logo: 'F'
  });

  useEffect(() => {
    // Load company settings from API
    const fetchSettings = async () => {
      try {
        const response = await fetch('/api/admin/settings');
        if (response.ok) {
          const data = await response.json();
          if (data.success && data.settings) {
            setCompanySettings({
              name: data.settings.name || 'FrameShop',
              logo: data.settings.logo || 'F'
            });
          }
        } else {
          console.error('Failed to fetch settings from API');
        }
      } catch (error) {
        console.error('Error loading company settings:', error);
        // Fallback to default settings
        setCompanySettings({
          name: 'FrameShop',
          logo: 'F'
        });
      }
    };

    fetchSettings();
  }, []);

  const menuItems = [
    { href: '/admin/dashboard', label: 'Dashboard', icon: 'üìä' },
    { href: '/admin/products', label: 'Products', icon: 'üì¶' },
    { href: '/admin/categories', label: 'Categories', icon: 'üìÇ' },
    { href: '/admin/orders', label: 'Orders', icon: 'üõí' },
    { href: '/admin/customers', label: 'Customers', icon: 'üë•' },
    { href: '/admin/reports', label: 'Reports', icon: 'üìà' },
    { href: '/admin/bulk-upload', label: 'Bulk Upload', icon: 'üì§' },
    { href: '/admin/settings', label: 'Settings', icon: '‚öôÔ∏è' },
  ];

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Top Navigation */}
      <nav className="bg-white shadow-sm border-b">
        <div className="max-w-full mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            <div className="flex items-center">
              <Button
                variant="ghost"
                size="sm"
                onClick={() => setSidebarOpen(!sidebarOpen)}
                className="mr-4"
              >
                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M4 6h16M4 12h16M4 18h16" />
                </svg>
              </Button>
              <Link href="/admin" className="flex items-center space-x-2">
                <div className="w-8 h-8 bg-black rounded-md flex items-center justify-center">
                  {companySettings.logo && companySettings.logo.startsWith('data:') ? (
                    <img 
                      src={companySettings.logo} 
                      alt="Logo" 
                      className="w-full h-full object-cover rounded-md"
                    />
                  ) : (
                    <span className="text-white font-bold text-sm">{companySettings.logo}</span>
                  )}
                </div>
                <span className="text-xl font-bold text-gray-900">{companySettings.name} Admin</span>
              </Link>
            </div>

            <div className="flex items-center space-x-4">
              <Badge variant="secondary">Admin</Badge>
              <span className="text-sm text-gray-700">Admin User</span>
              <Button
                variant="outline"
                size="sm"
                onClick={() => {
                  localStorage.removeItem('adminLoggedIn');
                  router.push('/admin/login');
                }}
              >
                Logout
              </Button>
            </div>
          </div>
        </div>
      </nav>

      <div className="flex">
        {/* Sidebar */}
        <aside className={`${sidebarOpen ? 'w-64' : 'w-16'} bg-white shadow-sm transition-all duration-300 min-h-screen`}>
          <nav className="mt-8">
            <div className="px-4 space-y-2">
              {menuItems.map((item) => (
                <Link
                  key={item.href}
                  href={item.href}
                  className="flex items-center px-4 py-3 text-gray-700 rounded-lg hover:bg-gray-100 transition-colors"
                >
                  <span className="text-xl mr-3">{item.icon}</span>
                  {sidebarOpen && <span className="font-medium">{item.label}</span>}
                </Link>
              ))}
            </div>
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1 p-8">
          <div className="max-w-7xl mx-auto">
            <div className="mb-8">
              <h1 className="text-3xl font-bold text-gray-900">{title}</h1>
              <p className="text-gray-600 mt-2">Manage your e-commerce store</p>
            </div>
            {children}
          </div>
        </main>
      </div>
    </div>
  );
}
